



import {

    GET_BLOG_LIST_FAIL,
    GET_BLOG_LIST_SUCCESS
} from '../actions/types'

const initialState = {
    count: null,
    next: null,
    previous: null,
    blog_list: null

}

export default function Blog(state=initialState, action) {

    const {type, payload} = action;

    switch(type) {
        case GET_BLOG_LIST_SUCCESS:
            return {
                ...state,
                count: payload.count,
                next: payload.next,
                previous: payload.previous,
                blog_list: payload.results.posts

            }
        
        case GET_BLOG_LIST_FAIL:
            return {
                ...state,
                count: null,
                next: null,
                previous: null,
                blog_list: null
            }

        default: 
            return state
    }


}